@import '../../../../styles/sass/mixins';
@import '../../../../styles/sass/vars';

.wrap {
  width: 100%;
  min-height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 99999;
  background-color: rgba(56, 51, 51, 0.192);
  @include flex(flex, row, center, center);

  .card {
    margin: 0 auto;
    @include flex(flex, column, center, center);
    max-width: 345px;
    background-color: #fff;
    border-radius: 5px;
  }

  .titleWrap {
    @include flex(flex, row, space-between, center);
    width: 100%;
    margin-top: 15px;
    border-bottom: 1px solid #EDF0F6;

    .title {
      @include text(18px, 600, 27px, $textColor);
      margin-left: 25px;
    }

    .closeWrap {
      display: block;
      margin-right: 25px;

      .close {
        position: relative;
        width: 20px;
        height: 20px;
        opacity: 0.3;

        &:hover {
          opacity: 1;
        }

        &:before, &:after {
          position: absolute;
          top: -11px;
          left: 0;
          content: ' ';
          height: 20px;
          width: 2px;
          background-color: #333;
        }

        &:before {
          transform: rotate(45deg);
        }

        &:after {
          transform: rotate(-45deg);
        }
      }
    }
  }

  .text {
    display: block;
    margin: 20px 25px;
    @include text(13px, 400, 24px, $textColor);
  }

  .btnWrap {
    width: 85%;
    margin: 20px 0;
    @include flex(flex, row, space-between, center);

    .btn {
      padding: 8px 35px;
      border: none;
      border-radius: 20px;
      background-color: orangered;

      &:hover {
        opacity: .7;
      }
    }
  }
}