@import './../../../../../styles/sass/mixins.scss';
@import './../../../../../styles/sass/vars.scss';

.set {
  background: $backgroundColor;
  width: 100%;
  height: 100vh;
  @include flex(flex, row, center, center)
}

.card {
  max-width: 415px;
  background-color: #fff;
  border-radius: 20px;
  @include flex(flex, column, center, center);
}

.logo {
  margin-top: 25px;
}

.form {
  @include flex(flex, column, center, center);
  margin: 40px 30px 70px 30px;

  .text {
    display: block;
    margin-top: 30px;
    @include text(16px, 400, 24px, #2D2E46);
  }

  .Btn {
    background-color: rgb(48, 64, 206);
    border: none;
    border-radius: 20px;
    color: #fff;
    height: 35px;
    max-width: 265px;
    margin-top: 90px;
    font-size: 16px;

    &:hover {
      background-color: rgb(82, 97, 235);
    }
  }
}


